#mosaic {
  grid-template-columns: repeat(6, 1fr);
  grid-auto-rows: minmax(auto, 200px);
  grid-gap: 0.6rem;

  display: grid;
  margin: 0 auto;

  div {
    background: #333;
    padding: 1rem;
    color: #fff;
  }

  .m1 {
    grid-column: 1 / 3;
    grid-row: 1/5;
  }

  .m2 {
    grid-column: 3/7;
    grid-row: 1/3;
  }

  .m3 {
    grid-column: 3/5;
    grid-row: 3/5;
  }

  .m4 {
    grid-column: 5/7;
    grid-row: 3/7;
  }

  .m5 {
    grid-column: 1/5;
    grid-row: 5/7;
  }

  img {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }

}

.m1, .m2, .m3, .m4, .m5 {
  position: relative;
  cursor: pointer;

  .overlay {
    transition: .5s ease;
    background-color: var(--contrast);
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &:hover .overlay {
    opacity: 0.6;
  }

  .text {
    color: white;
  }
}

@media (max-width: map-get($breakpoints, "sm")) {
  #mosaic {
    display: block;

    div {
      margin-bottom: 0.6rem;
    }
  }
}
